import {
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild
} from '@angular/core';
import { IManagedObject } from '@c8y/client';
import {
  ActionControl,
  BuiltInActionType,
  BulkActionControl,
  Column,
  CustomColumnConfig,
  DataGridComponent,
  DataSourceModifier,
  FilteringActionType,
  FilteringModifier,
  gettext,
  GridConfig,
  HeaderActionControl,
  LoadMoreMode,
  Pagination,
  Row,
  ServerSideDataResult
} from '@c8y/ngx-components';
import { BehaviorSubject, combineLatest, Observable, of, Subject } from 'rxjs';
import {
  concatMap,
  filter as filterRxJS,
  first,
  map,
  switchMap,
  take,
  takeUntil,
  tap
} from 'rxjs/operators';
import { DataCallback, DeviceGridColumn, FilterConfig } from './device-grid.model';
import { DeviceGridService } from './device-grid.service';

@Component({
  selector: 'c8y-device-grid',
  templateUrl: './device-grid.component.html'
})
export class DeviceGridComponent implements OnInit, OnDestroy {
  /** Optional callback function that allows to modify server side data result before it's rendered. */
  @Input() dataCallback: DataCallback;
  /** Takes an event emitter. When an event is emitted, the grid will be reloaded. */
  @Input() refresh: EventEmitter<any> = new EventEmitter();
  /** The title for the data grid, it's displayed in the grid's header. */
  @Input() title: string = gettext('Devices');
  /** The label for load more button. */
  @Input() loadMoreItemsLabel = gettext('Load more devices');
  /** The label for loading indicator. */
  @Input() loadingItemsLabel: string = gettext('Loading devices');
  /** The name of the key where columns configuration will be stored. */
  @Input() columnsConfigKey: string;
  /** The list of columns to be displayed in the grid. If not given, it defaults to standard columns. */
  @Input('columns') columnsConfig: DeviceGridColumn[];
  /** Pagination settings, e.g. allows for setting current page or page size. If not given, defaults to standard settings. */
  @Input('pagination') set _pagination(value: Pagination) {
    if (value) {
      this.pagination = value;
    } else {
      this.pagination = this.deviceGridService.getDefaultPagination();
    }
  }
  infiniteScroll: LoadMoreMode;
  /** Sets load more mode. */
  @Input('infiniteScroll') set _infiniteScroll(infiniteScroll: LoadMoreMode) {
    this.infiniteScroll = infiniteScroll;
    if (infiniteScroll) {
      this.pagination = this.deviceGridService.getInfiniteScrollPagination();
    }
  }
  /** Sets action controls (actions available for individual items). If not given, it defaults to standard actions. */
  @Input('actionControls') set _actionControls(value: ActionControl[]) {
    if (value) {
      this.actionControls = value;
    } else {
      this.actionControls = this.deviceGridService.getDefaultActionControls();
    }
  }
  /** Determines whether items can be selected by clicking a checkbox in the first column. */
  @Input() selectable: boolean = false;
  /** Sets the base query which is appended to the request for data. */
  @Input() baseQuery: any = {};
  /** Sets bulk action controls (actions available for items selected by user). If not given, it defaults to standard bulk actions. */
  @Input('bulkActionControls') set _bulkActionControls(value: BulkActionControl[]) {
    if (value) {
      this.bulkActionControls = value;
    } else {
      this.bulkActionControls = this.deviceGridService.getDefaultBulkActionControls();
    }
  }
  /** Sets header action controls (actions available from the grid header). If not given, it defaults to empty list of actions. */
  @Input('headerActionControls') set _headerActionControls(value: HeaderActionControl[]) {
    if (value) {
      this.headerActionControls = value;
    } else {
      this.headerActionControls = this.deviceGridService.getDefaultHeaderActionControls();
    }
  }
  @Input() childDeviceGrid: boolean = false;
  @Input() parentDeviceId: string;
  /** Sets the withChildren query which is appended to the request. */
  @Input() withChildren: boolean = false;
  /** Emits an event when columns configuration changes. */
  @Output() onColumnsChange: EventEmitter<DeviceGridColumn[]> = new EventEmitter<
    DeviceGridColumn[]
  >();
  @Output() onFilterChange: EventEmitter<FilterConfig[]> = new EventEmitter<FilterConfig[]>();
  @Output() onDeviceQueryStringChange: EventEmitter<string> = new EventEmitter<string>();
  /** Emits an event when items selection changes. The array contains ids of selected items. */
  @Output() itemsSelect = new EventEmitter<string[]>();

  configureColumnsEnabled: boolean = true;
  actionControls: ActionControl[] = [];
  columns$: Observable<DeviceGridColumn[]> = of(this.deviceGridService.getDefaultColumns());
  appliedFilters: FilterConfig[] = [];
  pagination: Pagination = this.deviceGridService.getDefaultPagination();
  bulkActionControls: BulkActionControl[] = this.deviceGridService.getDefaultBulkActionControls();
  headerActionControls: HeaderActionControl[] =
    this.deviceGridService.getDefaultHeaderActionControls();
  serverSideDataCallback: any;

  @ViewChild(DataGridComponent, { static: true })
  dataGrid: DataGridComponent;

  /**
   * Manages the requests to the DeviceGridService.saveConfig$() method:
   * 0 - Semaphore is in initial state, request can be executed;
   * 1 - Semaphore is blocked, further requests need to wait for lock to get released;
   * 2 - Semaphore is "green", requests can be done;
   */
  private saveGridConfigSemaphore$: BehaviorSubject<number> = new BehaviorSubject(0);
  private destroyed$: Subject<void> = new Subject();

  constructor(public deviceGridService: DeviceGridService) {
    this.serverSideDataCallback = this.onDataSourceModifier.bind(this);
  }

  ngOnInit() {
    this.setActionControls();

    if (this.columnsConfig && !this.columnsConfigKey) {
      this.columns$ = of(this.columnsConfig);
      this.configureColumnsEnabled = false;
      console.warn(
        'Device grid expects columnsConfigKey to be set if setting customized columns list. Without it the changes to the grid wll not be saved anywhere.'
      );
    } else {
      this.columns$ = this.deviceGridService.getUserConfiguredColumns$(
        this.columnsConfig ? this.columnsConfig : this.deviceGridService.getDefaultColumns(),
        this.columnsConfigKey
      );
    }
  }

  ngOnDestroy(): void {
    this.destroyed$.next();
    this.destroyed$.complete();
  }

  trackByName(_index, column: DeviceGridColumn): string {
    return column.name;
  }

  async onDataSourceModifier(
    dataSourceModifier: DataSourceModifier
  ): Promise<ServerSideDataResult> {
    let serverSideDataResult: ServerSideDataResult;
    let filteredSize: number;
    let size: number;
    if (this.childDeviceGrid) {
      const { res, data, paging } = await this.deviceGridService.getChildDeviceData(
        dataSourceModifier.columns,
        dataSourceModifier.pagination,
        this.baseQuery,
        this.withChildren,
        this.parentDeviceId
      );

      filteredSize = await this.deviceGridService.getCountChildDevices(
        dataSourceModifier.columns,
        dataSourceModifier.pagination,
        this.baseQuery,
        this.parentDeviceId
      );
      size = await this.deviceGridService.getTotalChildDevices(this.baseQuery, this.parentDeviceId);

      serverSideDataResult = {
        res,
        data,
        paging,
        filteredSize,
        size
      };
    } else {
      const { res, data, paging } = await this.deviceGridService.getData(
        dataSourceModifier.columns,
        dataSourceModifier.pagination,
        this.baseQuery,
        this.withChildren
      );

      filteredSize = await this.deviceGridService.getCount(
        dataSourceModifier.columns,
        dataSourceModifier.pagination,
        this.baseQuery
      );
      size = await this.deviceGridService.getTotal(this.baseQuery);

      serverSideDataResult = {
        res,
        data,
        paging,
        filteredSize,
        size
      };
    }

    this.onColumnsChange.emit(dataSourceModifier.columns);
    this.onDeviceQueryStringChange.emit(
      this.deviceGridService.getDeviceQueryString(dataSourceModifier.columns, this.baseQuery)
    );

    if (this.dataCallback) {
      serverSideDataResult = this.dataCallback(serverSideDataResult);
    }

    return serverSideDataResult;
  }

  filterChange({ columnName, filteringModifier }) {
    const index = this.appliedFilters.findIndex(el => el.columnName === columnName);

    if (index > -1) {
      this.appliedFilters[index].filteringModifier = filteringModifier;
    } else {
      this.appliedFilters.push({ columnName, filteringModifier });
    }

    this.onFilterChange.emit(this.appliedFilters);
    this.updateFilterConfig(columnName, filteringModifier);
  }

  updateFilterConfig(columnName, filteringModifier) {
    // Here I needed to take the current columns because if there isn't any stored config
    // and custom columns are provided through parent element we need to use them.
    combineLatest([this.deviceGridService.getConfig$(this.columnsConfigKey), this.columns$])
      .pipe(
        tap(([config, columns]) => {
          const columnsConfig = (config?.columns?.length > 0 ? config.columns : columns).map(
            ({ name, visible, sortOrder, filter, custom, header, path }) => ({
              name,
              visible: visible ?? true,
              sortOrder,
              custom,
              header,
              path,
              filter: columnName && columnName !== name ? filter : filteringModifier
            })
          );

          if (!columnsConfig.filter) {
            delete columnsConfig.filter;
          }

          this.configChange({ columns: columnsConfig, pagination: config.pagination });
        }),
        take(1),
        takeUntil(this.destroyed$)
      )
      .subscribe();
  }

  async setActionControls() {
    const deleteAction: ActionControl = {
      type: BuiltInActionType.Delete,
      callback: (device: Row) => this.onDeleteDevice(device as IManagedObject)
    };
    this.actionControls.push(deleteAction);
  }

  onDeleteDevice(device) {
    this.deviceGridService
      .delete(device)
      .then(() => this.refresh.emit())
      .catch(() => ({}));
  }

  updateFiltering(
    columnNames: string[],
    action: {
      type: FilteringActionType;
      payload?: { filteringModifier: FilteringModifier };
    }
  ) {
    const { type } = action;
    if (type === FilteringActionType.ResetFilter) {
      this.dataGrid.clearFilters();
    } else {
      this.dataGrid.updateFiltering(columnNames, action);
    }
  }

  configChange(config: GridConfig) {
    /* Semaphore ensures that the first request to DeviceGridService.saveConfig$() is completed
     * before the next requests are sent out. This prevents multiple configuration objects to be
     * created in Inventory in case a config does not yet exist.
     */
    this.saveGridConfigSemaphore$
      .pipe(
        filterRxJS(semaphore => semaphore !== 1),
        tap(() => {
          if (this.saveGridConfigSemaphore$.value < 1) {
            this.saveGridConfigSemaphore$.next(this.saveGridConfigSemaphore$.value + 1);
          }
        }),
        concatMap(() => this.deviceGridService.saveConfig$(config, this.columnsConfigKey)),
        tap(() => {
          if (this.saveGridConfigSemaphore$.value < 2) {
            this.saveGridConfigSemaphore$.next(this.saveGridConfigSemaphore$.value + 1);
          }
        }),
        take(1),
        takeUntil(this.destroyed$)
      )
      .subscribe();
  }

  addCustomColumn(columnConfig: CustomColumnConfig) {
    this.deviceGridService
      .getConfig$(this.columnsConfigKey)
      .pipe(
        tap(
          config =>
            (config.columns = [
              ...(config.columns.length === 0 ? this.getDefalultConfigColumns() : config.columns),
              columnConfig
            ])
        ),
        concatMap(config => this.deviceGridService.saveConfig$(config, this.columnsConfigKey)),
        switchMap(() =>
          this.deviceGridService.getUserConfiguredColumns$(this.columns$, this.columnsConfigKey)
        ),
        // Let data-grid re-render with new columns;
        // Columns are set in a separate step in the pipe to avoid flickering
        // in the grid in the time where get config request is executing
        tap(columns => (this.columns$ = of(columns))),
        take(1),
        takeUntil(this.destroyed$)
      )
      .subscribe();
  }

  removeCustomColumn(columnToRemove: Column) {
    this.deviceGridService
      .getConfig$(this.columnsConfigKey)
      .pipe(
        first(),
        tap(
          config =>
            (config.columns = config.columns.filter(column => column.name !== columnToRemove.name))
        ),
        concatMap(config => this.deviceGridService.saveConfig$(config, this.columnsConfigKey)),
        switchMap(() =>
          this.deviceGridService.getUserConfiguredColumns$(
            this.columns$.pipe(
              map(columns => columns.filter(column => column.name !== columnToRemove.name))
            ),
            this.columnsConfigKey
          )
        ),
        // Let data-grid re-render with new columns;
        // Columns are set in a separate step in the pipe to avoid flickering
        // in the grid in the time where get config request is executing
        tap(columns => (this.columns$ = of(columns))),
        take(1),
        takeUntil(this.destroyed$)
      )
      .subscribe();
  }

  private getDefalultConfigColumns() {
    return this.deviceGridService
      .getDefaultColumns()
      .map(column => ({ name: column.name, visible: true }));
  }
}
