import { BaseDeviceGridColumn } from './base.device-grid-column';
import { gettext, ColumnConfig, getBasicInputArrayFormFieldConfig } from '@c8y/ngx-components';

export class SystemIdDeviceGridColumn extends BaseDeviceGridColumn {
  constructor(initialColumnConfig?: ColumnConfig) {
    super(initialColumnConfig);

    const self = this;
    this.path = 'id';
    this.name = 'systemId';
    this.header = gettext('System ID');

    this.filterable = true;
    this.filteringConfig = {
      fields: getBasicInputArrayFormFieldConfig({
        key: 'ids',
        label: gettext('Show items with system ID'),
        addText: gettext('Add next`id`'),
        tooltip: gettext('Use * as a wildcard character'),
        placeholder: '10300'
      }),
      getFilter(model: any): any {
        const filter: any = {};
        if (model.ids.length) {
          filter[self.path] = { __in: model.ids };
        }
        return filter;
      }
    };

    this.sortable = true;
    this.sortingConfig = {
      pathSortingConfigs: [{ path: this.path }]
    };
  }
}
